.tutorial {
    background-color: black;
    /* background-image: linear-gradient(to bottom right, black, rgb(49, 48, 48)); */
    padding: 5%;
  }
  .tutorial-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .content {
    padding-top: 10px;
    font-size: 40px;
    color: white;
  }
  
  .button {
    font-size: 100%;
    background: transparent;
    /* background: #4871b6; */
    color: #ffffff;
    cursor: pointer;
    padding: 18px 36px;
    vertical-align: middle;
    border: 1px solid white;
    margin-top: 50px;
    border-radius: 6px;
    font-weight: bold;
  }